<template>
    <div class="box">
        <Header :got="true" :amt="true" :aot="false" :ant="false" :gnt="false"  :cot="false"></Header>
        <div class="content">
            <div class="hot">
                <ul>
                    <li>
                        <van-search  placeholder="请输入商家或美食名称" v-model="value" />
                    </li>
                    <li><van-button type="info" @click="add()" class="but"><h4>提交查询</h4></van-button></li>
                </ul>
            </div>
            <div class="item" v-if="content.length==0">
                <div class="search"><h4>搜索历史</h4></div>
                <div class="history" v-for="(item,index) in info" :key="index" @click="search()">
                    <div>{{item}}</div>
                    <div @click="del(index)"><h3>x</h3></div>
                </div>
                <div class="search"><van-button class="van" @click="clear()" type="info"><h4>清除全部</h4></van-button></div>
            </div>
            <div class="takeaway" v-for="(item,index) in content" :key="index" v-if="content.length>0">
                <div class="img">
                    <img :src="'//elm.cangdu.org/img/'+item.image_path" alt="">
                </div>
                <div class="payment">
                    <ul>
                        <li>
                            <div>{{item.name}}</div>
                            <div><svg data-v-3726cde6="" xmlns="http://www.w3.org/2000/svg" version="1.4" width="100" height="15" class="pay_icon"><polygon data-v-3726cde6=""
                                 points="0,14 4,0 24,0 20,14" style="fill: none; stroke: rgb(255, 96, 0);stroke-width: 1px;"></polygon> <line data-v-3726cde6="" x1="1.5" y1="12" x2="20" y2="12" 
                                 style="stroke: rgb(255, 96, 0); stroke-width: 1.5px;"></line> <text data-v-3726cde6="" x="3.5" y="9" style="fill: rgb(255, 96, 0); font-weight: bold;">支付</text></svg>
                            </div>
                        </li>
                        <li>月售 {{item.recent_order_num}} 单</li>
                        <li>{{item.float_minimum_order_amount}}元起送 / 距离{{item.distance}}公里</li>
                    </ul>
                </div>
            </div>
            <div v-if="content == 1">很抱歉！访问错误</div>
            
        </div>
        <Footer ></Footer>
    </div>
</template>

<script>
import Header from '../header/Header'
import Footer from '../footer/Footer'
    export default {
        data() {
            return {
                value:'',
                localStr:[],
                info:[],
                content:[]
            }
        },
        components: {
            Header,
            Footer
        },
        methods: {
            add(){
                let geohash=this.$route.params.id
                if(this.value){
                    this.http.get(`http://elm.cangdu.org/v4/restaurants?extras[]=restaurant_activity&geohash=${geohash}&keyword=${this.value}&type=search`).then((res)=>{
                        console.log(res.data)
                        if(res.data.length == 0 || res.data.status == 0){
                           this.content = 1
                        }else{
                            this.content = res.data
                        }
                    })
                }
                let obj = [...this.info]
                let zhi = obj.some((v)=>{
                    return v == this.value
                })
                if(zhi == false && this.value == ""){
                     
                }else{
                    obj.push(this.value)
                }
                localStorage.setItem('localStr',JSON.stringify(obj))
                this.info = obj
                this.value = ''
            },
            del(index){
                var sot = [...this.info]
                sot.splice(index,1)
                localStorage.setItem('localStr',JSON.stringify(sot))
                this.info=JSON.parse(localStorage.getItem('localStr'))
            },
            clear(){
                this.info = []
                localStorage.removeItem('localStr')
            }
        },
        mounted() {
            let obj=JSON.parse(localStorage.getItem('localStr'))
            if(obj !=null){
                this.info = obj
            }
        },
    }
</script>

<style lang="scss" scoped>
    .takeaway{width: 100%;height: 2rem;border: 0.001rem solid rgb(224, 207, 207);display: flex;
        font-size: 0.3rem;
        .img{
            flex: 1;
            img{
                width: 1.3rem;
                margin: 0.2rem;
            }
        }
        .payment{
            margin: 0.15rem;
            flex: 4;
            ul{
                width: 100%;
                li{
                    padding-top: 0.1rem;
                }
                li:nth-of-type(1){
                    display:flex;
                    div:nth-of-type(2){
                        padding-left: 0.2rem;
                    }
                }
            }
        }
    }
    .box{
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        font-size: 0.35rem;
    }
    .van{
        width: 100%;
        background: white;
        color: rgb(161, 140, 180);
        font-size: 0.37rem;
        border: none;
    }
    .but{
        height: 0.75rem;
        margin-top: 0.15rem;
    }
    .content{
        width: 100%;
        flex: 1;
        overflow: auto;
        background: rgb(247, 243, 243);
        .hot{
            width: 100%;
            height: 1.4rem;
            background: white;
            ul{
                width: 100%;
                display: flex;
                li:nth-of-type(1){
                    flex: 3;
                    margin: 0.3rem 0.2rem;
                }
                li:nth-of-type(2){
                    flex: 1;
                     border-radius: 0.15rem;
                    margin: 0.3rem 0.1rem;
                }
            }
        }
        .item{
            width: 100%;
            .search{
                width: 100%;
                height: 1rem;
                line-height: 1rem;
                padding: 0 0.3rem;
                color: rgb(172, 143, 143);
            }
            .history{
                width: 100%;
                height: 1rem;
                background: white;
                display: flex;
                div{
                    line-height: 1rem;
                    margin: 0 0.3rem;
                }
                div:nth-of-type(1){
                    flex: 10;
                }
                div:nth-of-type(2){
                    flex: 1;
                    color: rgb(172, 143, 143);
                }
            }
        }
    }
</style>


